@use '/src/base/config';

.contact{
  background-color: config.$dark-color;
  text-align: center;
  height: 90vh;
  &__form{
    height: 100%;
  }
  &__map{
    padding-bottom: 5rem;
    &>iframe{
      display: none;
      @include config.laptop{
        margin-inline: auto;
        display: inherit;
        height: 230px;
        width: 500px;
        
      }
    }
  }
  @include config.laptop{
    position: relative;
    height: 72vh;
  }
}

.form-contact {
  display: grid;
  gap: 30px;
  width: 320px;
  margin: 0 auto;
  justify-items: center;
  padding-top: 2rem;
  font-family: config.$main-font;
  
  @include config.laptop {
    grid-template-columns: repeat(2, 1fr);
    margin: 0 auto 2rem;
    width: 50%;
  }

  &__fields {
    display: flex;
    flex-direction: column;
    height: 150px;
    justify-content: space-between;
    width: 100%;
  }

  &__input,
  &__textarea {
    font-family: config.$main-font;
    height: 40px;
    background-color: config.$dark-color;
    color: config.$text-color;
    border: 1px solid;
    outline: none;
    padding-left: 0.5rem;
  }

  &__textarea {
    resize: none;
    width: 320px;
    height: 150px;
    padding: 0.5rem;
  }
  &__button{
    padding: .5rem 1rem;
    font-size: 1rem;
    border: 0;
    border-radius: .2rem;
    font-weight: config.$font-bold;
    color: config.$text-color;
    background-color: config.$primary-color;
    cursor: pointer;
  }
}

.social-media-container{
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 50%;
  margin-bottom: 1rem;
  @include config.laptop{
    width: 30%;
  }
}